<style>
  .container {
    padding:10px;
  }
  Table{
    width:800px;
  }
</style>
<template>
  <!--建筑物管理->建筑物-->
  <!--右侧内容总div-->
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
        <span class="page-title">人员管理</span>
      </iCol>
    </Row>
    <div class="back-to-list" v-if="canReturn" @click="returnPage"></div>
    <!--右侧工具栏，包括检索、添加等按钮-->
    <div class="tool-panel">
      <Input v-model="searchParam1" class="grey" placeholder="名称/手机号" @on-enter="getPersonList(1)" style="width: 300px"></Input>
      <Button type="primary" @click="getPersonList(1)" class="grey" style="margin-left: 5px; width: 80px;">搜索</Button>
      <Button v-if="editable==true"  type="primary"@click="openAdd" class="grey" style="margin-left: 5px; width: 80px;float: right">添加</Button>
    </div>
    <!--右侧表格模块-->
    <div class="body-table">
      <!--表格位置-->
      <Table :columns="columns1" border :data="usersList"
             class="auto-height-table grey" style="width:100%"></Table>
      <!--分页位置-->
      <div style="margin-top: 10px;overflow: hidden">
        <div style="float: right;">
          <Page @on-change="getPersonList" class="default" :total="pageTotal" :current="pageNum" :pageSize="pageSize" :show-total="true"></Page>
        </div>
      </div>
    </div>

    <!--添加新员工-->
    <Modal v-model="addUserModal" :title="title" width="800" class="default" :mask-closable="false">
      <div class="tool-panel">
        <Form ref="searchFrom">
          <Row>
            <iCol span="8">
              <Form-item>
                <Input :disabled="!editable" v-model="searchParam3"  class="grey" placeholder="输入待邀请的员工手机号/姓名" @on-enter="searchNewPersonList(1)"></Input>
              </Form-item>
            </iCol>
            <iCol span="4">
              <Button type="primary" @click="searchNewPersonList(1)" class="grey" style="margin-left: 21px;margin-top: 4px; width: 80px;">搜索</Button>
            </iCol>
            <iCol span="12">
              <Button type="primary"  @click="openCreate" class="grey" style="margin-left: 5px; margin-top: 4px; padding: 0px; width: 80px; float: right">创建新员工</Button>
            </iCol>
          </Row>
        </Form>
      </div>
      <!--右侧表格模块-->
      <div class="body-table" style="margin-top:15px;">
        <!--表格位置-->
        <Table :columns="columns2" border :data="inviteUserList"
               class="auto-height-table grey" style="width:100%"></Table>
      </div>
      <!--分页位置-->
      <div style="margin-top: 10px;overflow: hidden">
        <div style="float: right;">
          <Page @on-change="searchNewPersonList" class="default" :total="pageTotal1" :current="pageNum1" :pageSize="pageSize1" :show-total="true"></Page>
        </div>
      </div>
      <div slot="footer"style="margin-top: -15px">
        <Button class="grey cancel" @click="addUserModal = false">取消</Button>
      </div>
    </Modal>
    <!--员工model-->
    <Modal v-model="userModal" :title="title" width="750" class="default" :mask-closable="false">
      <Form ref="userFrom":model="user" :label-width="70"  :rules="userValidate">
        <Row>
          <iCol span="12">
            <Form-item prop="name" label="名称："  style="margin-left:-10px;">
              <Input :disabled="!editable" v-model="user.name" placeholder="输入名称" :readonly ="editFlg"  class="grey" style="width:210px"/>
            </Form-item>
          </iCol>
          <iCol span="12">
          <Form-item prop="tel" label="电话：" style="margin-left:-10px;">
            <Input :disabled="!editable" v-model="user.tel" :readonly ="editFlg" placeholder="输入电话" class="grey"  style="width:210px"/>
          </Form-item>
          </iCol>
        </Row>
        <Form-item prop="depName" label="部门：" style="margin-left: -10px">
          <Select :disabled="!editable" v-model="user.depId" clearable placeholder="选择部门" size="small" class="grey" style="width:210px">
            <Option v-for="depItem in depList" :value="depItem.depId" >{{ depItem.depName }}</Option>
          </Select>
        </Form-item>
        <Row style="margin-top: 10px;">
          <iCol span="2" style="text-align: left;"><span style="margin-left: 7px; font-size: 14px">角色：</span></iCol>
        </Row>
      </Form>
      <!--表格位置-->
      <Table :columns="columns3" ref="selection" border :data="roleList"
             class="auto-height-table grey" style="width:100%; height: 260px;"></Table>
      <div slot="footer">
        <Button class="grey cancel" @click="returnPersonList">返回</Button>
        <Button v-if="editable==true && title=='创建新员工'" type="primary"class="grey" style="margin-left: 15px; width: 80px;" @click="addPerson('userFrom')">保存</Button>
        <Button v-if="editable==true && title=='邀请新员工'" type="primary"class="grey" style="margin-left: 15px; width: 80px;" @click="inviteSave('userFrom')">保存</Button>
        <Button v-if="editable==true && title=='员工信息'" type="primary"class="grey" style="margin-left: 15px; width: 80px;" @click="updPerson('userFrom')">保存</Button>
        <Button v-if="editable==true && title=='重新邀请'" type="primary"class="grey" style="margin-left: 15px; width: 80px;" @click="reInviteSave('userFrom')">保存</Button>
      </div>
    </Modal>
    <!--删除确认-->
    <Modal v-model="modal" class="default" :title="'确定要删除吗'" width="500" :mask-closable="false">
      <div slot="footer">
        <Button class="grey cancel" @click="modal = false">取消</Button>
        <Button class="grey" @click="delPerson">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import server from "../../../config/api";
  import Vue from 'vue';
  import niceScroll from 'jquery.nicescroll';
  export default {
    data(){
      return {
        canReturn: this.getYunSessionLength('route_list') > 1,
        depId:undefined,
        searchParam3:'',
        pageTotal1:undefined,
        pageNum1:undefined,
        pageSize1:8,
        pageSize:20,
        pageNum: undefined,
        organizationId:'',
        newRoles:[],
        modal:false,
        person:{},
        usersList:[],
        inviteUserList:[],
        roleList:[],
        depList:[],
        depItem:{
          depId:'',
          depName:''
        },
        addUserModal:false,
        userModal:false,
        title:'',
        self: this,
        pageTotal: undefined,
        searchParam1: '',
        user:{},
        editFlg:false,
        userValidate:{
          name: [
            { required: true, message: '姓名不能为空', trigger: 'blur' }
          ],
          tel: [
            { required: true, message: '电话不能为空', trigger: 'blur' }
          ],
        },

        columns1: [
          {
            title: '序号',
            type: 'index',
            align: 'center',
            width:50
          },
          {
            title: '员工名称',
            key: 'name',
            align: 'center',
            width:80,
            render:(h,params)=>{
              return h('div',[
                h('a',{
                  on:{
                    click:()=>{
                      this.editPerson(params.row);
                    }
                  }
                },params.row.name)
              ]);
            }
          },
          {
            title: '手机号',
            key: 'tel',
            align: 'center',
            width:100
          },
          {
            title: '部门',
            key: 'depName',
            align: 'center'
          },
          {
            title: '角色',
            key: 'roleName',
            align: 'center',
            render:(h,params)=>{
              var roleName = [];
              if(params.row.roles.length==0){
                return null;
              }
              for(var i=0;i<params.row.roles.length;i++){
                roleName.push(params.row.roles[i].roleName);
              }
              return roleName.join(',');
            }
          },
          {
            title: '状态',
            key: 'state',
            align: 'center',
            width:90,
            render:(h,params)=>{
              let result = undefined;
              if(params.row.status === 'ACTIVE') {
                result = '已加入';
              } else if(params.row.status === 'PENDING'){
                result = '邀请中';
              } else if(params.row.status === 'REJECT'){
                result = '拒绝邀请';
              }
              return h('span', result);
            }
          },
        ],
        columns2: [
          {
            title: '姓名',
            key: 'name',
            align: 'center'
          },
          {
            title: '手机号',
            key: 'tel',
            align: 'center'
          },
          {
            title: '操作',
            align: 'center',
            width:80,
            render:(h,params)=>{
              return h('div', [
                h('a',{
                  style:{
                    color:'#a9b2fd'
                  },
                  on:{
                    click:()=>{
                      this.openInvite(params.row)
                    }
                  }
                },'邀请')
              ]);
            }
          }
        ],
        columns3: [
          {
            type: 'selection',
            width: 60,
            align: 'center',
          },
          {
            title: '角色',
            key: 'roleName',
            align: 'center'
          },
          {
            title: '描述',
            key: 'description',
            align: 'center'
          }
        ],
        columns6: [
          {
            title: '角色',
            key: 'roleName',
            align: 'center'
          },
          {
            title: '描述',
            key: 'description',
            align: 'center'
          }
        ]
      }
    },
    computed:{
      editable: function() {
        return this.hasPermission(this.$YDAuthCode.NormalEntity.AuthMgmt);
      }
    },
    methods: {
      /*获取角色*/
      getRoleList:function () {
        this.roleList=[];
        this.$http.get(server.api.organization.role.getRoleList.replace('{orgId}', this.organizationId)).then((response)=>{
          this.roleList = response.body.data;
        },(error)=>{
          this.$Message.error('角色获取失败!');
        });
      },
      /*获取部门*/
      getDepList:function () {
        this.$http.get(server.api.organization.department.getDepList.replace('{orgId}', this.organizationId)).then((response)=>{
          this.depList = response.body.data;
        },(error)=>{
          this.$Message.error('部门获取失败!');
        });
      },
      /*获取人员列表*/
      getPersonList:function (pageNum) {
        this.$http.get(server.api.organization.person.getPersonList.replace('{orgId}',this.organizationId)+"?searchParam="+this.searchParam1+"&pageSize="+this.pageSize+"&pageNum="+pageNum).then((response)=>{
          this.usersList = response.body.data.rows;
          this.pageTotal = response.body.data.totalCount;
          this.pageNum = pageNum;
          Vue.nextTick(() => {
            $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          });
        },(error)=>{
          this.$Message.error('人员列表获取失败!');
        });
      },
      /*创建人员列表*/
      createUserTable:function () {
        this.getRoleList(1);
        this.getDepList();
        this.getPersonList(1);
      },
      returnPage: function() {
        // 根据sessionStorage返回上一页
        this.popYunSession('route_list');
        let page = this.topYunSession('route_list');
        this.$router.push(page.router);
      },
//      returnUpperLevel: function () {
//        // 返回列表
//        this.$router.push({
//          path: '/archives',
//        });
//      },
      searchNewPersonList:function (pageNum) {
        this.$http.get(server.api.organization.person.searchNewPersonList.replace('{orgId}', this.organizationId)+"?searchParam="+this.searchParam3+"&pageSize="+this.pageSize1+"&pageNum="+pageNum).then((response)=>{
          this.inviteUserList = response.body.data.rows;
          this.pageTotal1 = response.body.data.totalCount;
          this.pageNum1 = pageNum;
        },(error) => {
          this.$Message.error('人员列表获取失败!');
        })
      },
      openAdd:function () {
        this.searchParam3='';
        this.inviteUserList=[];
        this.editFlg=false;
        this.title="添加新员工";
        this.addUserModal=true;
      },
      openCreate:function () {
        this.title="创建新员工";
        this.user={};
        this.clearRoles();
        this.userModal=true;
      },
      addPerson:function (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.user.pid=localStorage.default_pid;
            this.user.orgId=this.organizationId;
            this.setRoles();
            this.user.roles=this.newRoles;
            this.$http.post(server.api.organization.person.addPerson, this.user).then((response)=>{
              this.userModal=false;
              this.addUserModal=false;
              this.$Message.success('新员工创建成功!');
              this.getPersonList(this.pageNum);
            },(error)=>{
              this.$Message.error('新员工创建失败!');
            });
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },
      openInvite:function (user) {
        this.title="邀请新员工";
        this.editFlg=true;
        this.user=user;
        this.clearRoles();
        this.userModal=true;
      },
      inviteSave:function (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.user.pid=localStorage.default_pid;
            this.user.orgId=this.organizationId;
            this.setRoles();
            this.user.roles=this.newRoles;
            this.$http.post(server.api.organization.person.inviteSave, this.user).then((response)=>{
              this.userModal=false;
              this.addUserModal=false;
              this.getPersonList(this.pageNum);
            },(error)=>{
              this.$Message.error('邀请创建失败!');
            });
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },

      editPerson:function (user) {
        this.depId=user.depId;
        this.title="员工信息";
        this.editFlg=true;
        this.user=user;
        for(let index in this.roleList) {
          let i = _.findIndex(user.roles, {"roleId": this.roleList[index].roleId});
          let role = this.roleList[index];
          if(i === -1) {
            role._checked = false;
          } else {
            role._checked = true;
          }
          if(this.editable===false){
            role._disabled = true;
          }
          Vue.set(this.roleList, index, role);
        }
        this.userModal=true;
      },
      updPerson:function (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.user.pid=localStorage.default_pid;
            this.user.orgId=this.organizationId;
            this.setRoles();
            this.user.roles=this.newRoles;
            this.$http.put(server.api.organization.person.updPerson, this.user).then((response)=>{
              this.userModal=false;
              this.addUserModal=false;
              this.getPersonList(this.pageNum);
            },(error)=>{
              this.$Message.error('新员工修改失败!');
            });
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },
      quitPerson:function (user) {
        this.person=user;
        this.modal=true;
      },

      delPerson:function () {
        if(this.usersList.length==1 && this.pageNum !=1){
          this.pageNum=this.pageNum-1;
        }
        this.$http.post(server.api.organization.person.quitPerson, this.person).then((response)=>{
          this.getPersonList(this.pageNum);
          this.modal=false;
        },(error)=>{
          this.$Message.error('新员工创建失败!');
        });
      },

      openReInvite:function (user) {
        this.title="重新邀请";
        this.user=user;
        this.editFlg=true;
        for(let index in this.roleList) {
          let i = _.findIndex(user.roles, {"roleId": this.roleList[index].roleId});
          let role = this.roleList[index];
          if(i === -1) {
            role._checked = false;
            Vue.set(this.roleList, index, role);
          } else {
            role._checked = true;
            Vue.set(this.roleList, index, role);
          }
        }
        this.userModal=true;
      },
      reInviteSave:function (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.user.pid=localStorage.default_pid;
            this.user.orgId=this.organizationId;
            this.setRoles();
            this.user.roles=this.newRoles;
            this.$http.put(server.api.organization.person.reInviteSave, this.user).then((response)=>{
              this.userModal=false;
              this.getPersonList(this.pageNum);
            },(error)=>{
              this.$Message.error('重发请求失败!');
            });
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },
      clearRoles:function () {
        for(let index in this.roleList) {
          let role = this.roleList[index];
          role._checked = false;
          Vue.set(this.roleList, index, role);
        }
      },

      setRoles:function () {
        this.newRoles=[];
        this.newRoles=this.$refs.selection.getSelection();
      },

      returnPersonList:function () {
        this.user.depId=this.depId;
        this.userModal = false;
      }
    },

    mounted(){
      if(this.editable){
        this.columns1.push(
          {
            title: '操作',
            align: 'center',
            width:150,
            render:(h,params)=>{
              let state = params.row.status;
              if(state==='PENDING'|| state==='REJECT'){
                return h('div', [
                  h('i-button',{
                    props:{
                      type:'text',
                      size:'small'
                    },
                    style:{
                      color:'#f9caca'
                    },
                    on:{
                      click:()=>{
                        this.quitPerson(params.row)
                      }
                    }
                  },'移除'),

                  h('i-button',{
                    props:{
                      type:'text',
                      size:'small'
                    },
                    style:{
                      color:'#a9b2fd'
                    },
                    on:{
                      click:()=>{
                        this.openReInvite(params.row)
                      }
                    }
                  },'重发邀请')
                ]);
              } else {
                return h('div', [
                  h('i-button',{
                    props:{
                      type:'text',
                      size:'small'
                    },
                    style:{
                      color:'#f9caca'
                    },
                    on:{
                      click:()=>{
                        this.quitPerson(params.row)
                      }
                    }
                  },'移除')
                ]);
              }
            }
          }
        );
      }
      Vue.nextTick(() => {
        $(".auto-height-table .ivu-table-body").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "7px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
      });
      let page = this.topYunSession('route_list');
      if(page === null || page.orgId === undefined) {
        // 根节点
        this.organizationId = localStorage.current_org_id;
      } else {
        this.organizationId = page.orgId;
      }
      this.createUserTable();
    },
    watch:{
      userModal: function(val, oldVal) {
        if(val) {
          setTimeout(() => {
            $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          }, 450);
        }
      }
    }
  }
</script>



